body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace, "Space Grotesk", sans-serif;
}

.container {
  display: flex;
}

.left-side {
  background: url(./images/bg-main-desktop.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 35%;
}

.card-front {
  background: url(./images/bg-card-front.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 28vh;
  width: 72%;
  margin-top: 100px;
  position: relative;
  left: 220px;
  border-radius: 15px;
  color: hsl(0, 0%, 100%);
  box-shadow: 0px 0px 12px hsl(278, 68%, 11%);
}
.digits {
  font-size: 20px;
  letter-spacing: 2px;
  padding-left: 35px;
  padding-top: 14vh;
}
.card-back {
  background: url(./images/bg-card-back.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 28vh;
  width: 72%;
  margin-top: 25px;
  position: relative;
  left: 300px;
  border-radius: 15px;
  color: hsl(0, 0%, 100%);
  box-shadow: 0px 0px 12px hsl(278, 68%, 11%);
}

.card-back .back-num {
  font-size: 12px;
  letter-spacing: 2px;
  position: relative;
  left: 280px;
  top: 85px;
}

.below-digits {
  display: flex;
  justify-content: space-between;
  padding-left: 35px;
  padding-right: 35px;
  font-size: 12px;
  letter-spacing: 2px;
}

.below-digits .name {
  text-transform: uppercase;
}

.right-side {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 30%;
  margin-left: auto;
  margin-right: auto;
  /* background: red; */
}

.form {
  padding: 20px;
}

.form label {
  font-size: 12px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1px;
  padding-bottom: 5px;
  margin-top: 15px;
}

.form input {
  cursor: pointer;
}

.form input:focus {
  border: 1px solid hsl(278, 68%, 11%);
}

.form span {
  font-size: 12px;
  color: red;
  /* margin-bottom: 15px; */
  /* display: none; */
}

.card-name,
.card-num {
  height: 40px;
  font-size: 18px;
  border-radius: 10px;
  padding-left: 10px;
  padding-right: 10px;
  width: 95%;
  border: 1px solid hsl(270, 3%, 87%);
  outline: none;
}

.card-details {
  display: flex;
  justify-content: space-between;
}

.card-exp {
  display: flex;
  gap: 5px;
}

/* .card-exp span{
  display: inline-block;
  font-size: 12px;
  color: red;
} */

.date-and-time {
  width: 40px;
  height: 40px;
  font-size: 18px;
  border-radius: 12px;
  padding-left: 10px;
  padding-right: 10px;
  border: 1px solid hsl(270, 3%, 87%);
  outline: none;
}
.cvc {
  height: 40px;
  font-size: 18px;
  border-radius: 12px;
  padding-left: 10px;
  padding-right: 10px;
  width: 100%;
  outline: none;
  border: 1px solid hsl(270, 3%, 87%);
}

.form button {
  width: 100%;
  height: 45px;
  border-radius: 10px;
  border: none;
  outline: none;
  background: hsl(278, 68%, 11%);
  color: hsl(0, 0%, 100%);
  transition: 0.5 ease;
  cursor: pointer;
  margin-top: 30px;
  letter-spacing: 0.5px;
  font-size: 14px;
}

/* Responsiveness starts here */

/* Tablet */
@media (max-width: 991px) {
  .left-side {
    width: 30%;
  }

  .card-front {
    height: 18vh;
    width: 40vw;
    margin-top: 50px;
    position: relative;
    left: 100px;
  }

  .digits {
    padding-top: 9vh;
  }

  .card-back {
    height: 18vh;
    width: 40vw;
    margin-top: 50px;
    position: relative;
    left: 100px;
  }

  .card-back .back-num {
    top: 8vh;
    left: 250px;
  }
  .right-side {
    width: 50%;
  }
  .form {
    padding-top: 30vh;
  }
}

@media only screen and (max-width: 480px) {
  .container {
    flex-direction: column;
    height: 100vh;
  }
  .left-side {
    width: 100vw;
    height: 30vh;
  }
  .card-back .back-num {
    top: 9vh;
    left: 0;
    padding-left: 60vw;
  }
  .card-front {
    width: 80vw;
    position: relative;
    left: 15px;
    top: 100px;
  }
  .digits {
    padding-top: 7vh;
  }

  .card-back {
    width: 80vw;
    position: relative;
    left: 70px;
    margin-top: 0;
    bottom: 200px;
  }

  .right-side {
    width: 95%;
  }
  .form {
    padding-top: 10vh;
  }
}
